import { Watchlist } from "@rp/common/api-types"

const filterAndSortWatchlists = (watchlists: Array<Watchlist>, nameFilter?: string) => {
  return (
    watchlists
      // Filter wathlists by name
      .filter(watchlist =>
        nameFilter !== undefined
          ? watchlist.name.toLowerCase().includes(nameFilter.toLowerCase())
          : true,
      )
      // Sort watchilsts from newest to oldest
      .sort((firstWatchlist, secondWatchlist) => {
        if (firstWatchlist.lastUpdated < secondWatchlist.lastUpdated) {
          return 1
        }
        if (firstWatchlist.lastUpdated > secondWatchlist.lastUpdated) {
          return -1
        }

        return 0
      })
  )
}

export { filterAndSortWatchlists }
